import React from 'react';
import {
  Nav,
  NavLink,
  Bars,
  NavMenu,
  NavLink2
} from './NavbarElements';
import './Navbar.css';

const Navbar = () => {
  return (
    <>
      <Nav>
        <a href="http://worldwidecruisers.com">
          <img src={`${process.env.PUBLIC_URL}/wwclogo.png`} alt='logo' />
        </a>
        <Bars />
        <NavMenu>
          <NavLink as="a" href="http://worldwidecruisers.com/" activeStyle>
            Home
          </NavLink>
          <NavLink2 as="a" href="http://worldwidecruisers.com/build_your_own" class="active" activeStyle>
            Build your own
          </NavLink2>
          <NavLink as="a" href="http://worldwidecruisers.com/3-recent-builds" activeStyle>
            Recent builds
          </NavLink>
          <NavLink as="a" href="http://worldwidecruisers.com/4-merchandise" activeStyle>
            Merchandise
          </NavLink>
          <NavLink as="a" href="http://worldwidecruisers.com/content/4-about-us" activeStyle>
            About us
          </NavLink>
          <NavLink as="a" href="http://worldwidecruisers.com/contact-us" activeStyle>
            Contact us
          </NavLink>
        </NavMenu>
      </Nav>
    </>
  );
};

export default Navbar;