import { FaBars } from 'react-icons/fa';
import { NavLink as Link } from 'react-router-dom';
import styled from 'styled-components';

export const Nav = styled.nav`
  background: #fff;
  height: 70px;
  display: flex;
  justify-content: space-between;
  padding: 0.5rem calc((100vw - 1250px) / 2);
  font-size: 14px;
  line-height: 26px;
  margin-top: 30px;
  margin-bottom: 10px;
`;

export const NavLink = styled(Link)`
  color: #666;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 5px 20px;
  height: 100%;
  cursor: pointer;
  border: 1px solid transparent;
  border-radius: 30px;
  font-weight: 600;
  &.active {
    color: #EDD289;
    border-color: #EDD289;
  }
  &:hover {
    color: #EDD289;
    border-color: #EDD289;
    transition: color 300ms linear;
  }
`;

export const NavLink2 = styled(Link)`
  color: #000;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 5px 20px;
  height: 100%;
  cursor: pointer;
  border: 1px solid;
  border-radius: 30px;
  color: #EDD289;
  border-color: #EDD289;
  font-weight: 600;
`;

export const Bars = styled(FaBars)`
  display: none;
  color: #000;
  @media screen and (max-width: 768px) {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 75%);
    font-size: 1.8rem;
    cursor: pointer;
  }
`;

export const NavMenu = styled.div`
  display: flex;
  align-items: center;
  margin-right: 190px; 
  @media screen and (max-width: 768px) {
    display: none;
  }
  // padding: 30px;
  padding-top: 40px;
  padding-bottom: 10px;
`;